import insertOnlineVideo from '../commands/insertOnlineVideo';
import createRenderNode from '../createRenderNode';
import * as actions from '../actions';
import onAction from '../handlers/onAction';
import { BiPluginConfig, BiPlugin } from '../../../types';
import { moFactory } from '@ali/4ever-plugin-online-video';

const state = {};
function createOnlineVideoPlugin(config: BiPluginConfig): BiPlugin {
  const onlineVideoConfig = config.onlineVideo || {};
  return {
    ...moFactory({}),
    commands: {
      insertOnlineVideo: insertOnlineVideo(state),
    },
    renderNode: createRenderNode(onlineVideoConfig, state),
    onAction: onAction(state),
    models: {
      klass: 'block',
      type: 'onlineVideo',
    },
  };
}

createOnlineVideoPlugin.actions = actions;

export default createOnlineVideoPlugin;
